import React from 'react';

export default function Contract({ name, idNumber, address, email }: any) {
  return (
    <div className='x-font4'>
      It is recorded by this document, the Mutual Contract that they celebrate on the one hand,
      <span className="x-font5">{name}</span> - identified with Identity card DNI/RUC N° <span className="x-font5">{idNumber}</span> - residing in <span className="x-font5">{address}</span> with
      email <span className="x-font5">{email}</span> hereinafter, the Creditor, and of the other, the company ROI Capital Humano S.A.C., with R.U.C. No. 20606575077, with address at Calle Guido Reni No. 199 Department 201, district of San Borja, province and department of Lima, duly represented by Ms. Ruth Carolina León Gamboa, identified with DNI No. 40933729, as it appears in the powers of attorney registered in entry A00001, of entry No. 14534739, of Registry No. IX of Legal Entities of Lima and Callao – Lima Headquarters, which will hereinafter be referred to as ROI, in accordance with the following terms and conditions:

      FIRST: OF THE MUTUAL MONEY OBJECT OF THE CONTRACT

      THE CREDITOR grants as mutual money in favor of ROI the sum of US $270 (TWO HUNDRED AND SEVENTY-00/100 AMERICAN DOLLARS), in a single installment, payable upon signing this Agreement by Paypal.
      ROI as a closed corporation, receives the amount of money described above for the development of its social activity and recognizes the creditor as a surplus agent to whom it must return the amount borrowed plus the interest agreed upon in accordance with this contract.

      SECOND: INTERESTS

      The maximum amount to be paid as of March 16, 2024 for the loan, including interest, will be US$324 (THREE HUNDRED AND TWENTY-FOUR 00/100 AMERICAN DOLLARS).

      THIRD: RESPONSIBILITY FOR COMPLIANCE WITH OBLIGATIONS

      The Parties agree that the only party responsible for complying with the obligations arising from this Agreement and its Annexes is ROI. In this sense, it is established that the legal representative or agent(s) of the company will not be responsible for the obligations referred to.

      FOURTH: RECOGNITION OF OTHER DEBTS

      The Creditor Person expressly states that he acknowledges that ROI has other obligations in force and parallel to the one referred to in this Mutual Contract, which are independent from each other.

      FIFTH: ASSIGNMENT OF RIGHTS

      The Creditor Person expressly accepts that he/she may not transfer or assign the rights or obligations that correspond to him/her by virtue of this Mutual Contract, unless expressly authorized by ROI. In addition, regarding the payment of the debt, this may be made by a Third Party on behalf of ROI.

      SIXTH: FORM OF PAYMENT OF THE OBLIGATION:

      The obligation to return the credit will be accounted for from the time of deposit by THE CREDITOR to the ROI account detailed above.
      The form of payment of the debt will be made through a deposit or transfer to the account in dollars of the Creditor Person who will be affiliated with Paypal.

      SEVENTH: COMPENSATION

      The parties agree that the amount detailed in the first clause could be compensated with services provided by ROI (assessment of skills map for 5 people) as an express objective novation, which would be optional by the Creditor. Additionally, the investor has the right to carry out an evaluation to obtain the Skills map and the emotional coefficient.

      EIGHTH: CONFIDENTIALITY AND DATA PROTECTION

      The parties agree to maintain under terms of confidentiality all information contained in and that may arise from this Agreement. Likewise, they undertake to keep confidential any information of the Parties and/or their representatives and not to disclose it, observing at all times the fulfillment of the duty of professional secrecy.

      Likewise, the Parties undertake to safeguard and keep confidential the personal and/or sensitive data owned by them and/or any representative for the purposes of developing and executing this contract. In this sense, the Parties declare that they will adopt the applicable security measures for the protection of the information, in order to comply with the provisions of this contract.

      NINTH: CONTRACT TERMINATION: ADVANCE PAYMENT

      The Parties agree that ROI is authorized to make a payment prior to the date provided for in this contract, that is, before March 16, 2024, for the total capital loaned by the creditor, for which the compensatory interest only until the date of said payment. In this sense, the Creditor acknowledges that it will not claim any additional compensation amount, the contract being terminated at the time of the full advance payment.

      On the contrary, the Creditor undertakes to respect the payment date, making it impossible to demand capital or interest before the expiration of the agreed obligation. Therefore, it will not be possible to request payment until before that date, that is, before March 16, 2024.



      TENTH: ADDRESSES

      The Parties indicate as their real address that indicated in the introduction of this contract. Likewise, the Parties establish that the communications and/or notifications linked to this contract that are made through the physical or electronic means declared by the parties will be considered validly addressed for all legal purposes.

      ELEVENTH: CAPACITY AND CONSENT OF THE PARTIES

      The contracting parties state that they are in full use of their powers and in accordance with each and every one of the clauses of this contract and its Annex, without any defect of consent or fraud that invalidates or hinders its fulfillment. In this sense, they confirm the legal act, declaring that there is no cause for nullity or nullity.
      Likewise, the parties expressly indicate the following:
      i) having read each of the clauses, ii) understanding the legal scope of these clauses, iii) agreeing to enter into this contract in accordance with these clauses and iv) The deposit referred to in the first clause implies express acceptance of everything stated in this Contract.


      The parties sign this contract as a sign of agreement, on March 17, 2022.
    </div>
  )
}