import { useState } from 'react';
import Contract from "./contract";
import { useAllContext } from '../../contexts';
import Axios from 'axios';
// @ts-ignore
import spinerImg from '../../assets/spiner.gif';

export default function Register() {
  const [state] = useAllContext();
  const [loading, setLoading] = useState<boolean>();

  const handleBuyWithPaypal = () => {
    setLoading(true);
    Axios({
      method: "GET",
      url: "/api/users/buy-with-paypal",
      params: {
        user: state.user.id,
        amount: "127"
      }
    }).then(res => {
      console.log("res", res.data)
      window.location.href = res.data;
    })
  }
  return (
    <div className='x-container2 x-auth-container'>
      <div className='x-center x-margin1'>
        <button className="x-button2 x-font3" onClick={handleBuyWithPaypal}>{loading ? <img src={spinerImg} alt="spiner image" width="30px" /> : "Buy With Paypal"}</button>
      </div>
      <div className='x-margin'>
        <Contract name={state.userInfo.name} idNumber={state.userInfo.idNumber} address={state.userInfo.address} email={state.userInfo.email} />
      </div>
      <div className='x-center x-margin1'>
        <button className="x-button2 x-font3" onClick={handleBuyWithPaypal}>{loading ? <img src={spinerImg} alt="spiner image" width="30px" /> : "Buy With Paypal"}</button>
      </div>
    </div>
  )
}